.container {
   position: relative;
   top: 0;
   left: 0;
   width: 35px;
   height: 35px;
   flex-shrink: 0;
   margin-left: auto;
}
.icon {
   width: 100%;
   height: 100%;;
   object-fit: contain;
   object-position: center center;
}
.counter {
   position: absolute;
   top: -5px;
   right: -5px;
   display: flex;
   justify-content: center;
   align-items: center;
   width: 20px;
   height: 20px;
   font-size: 13px;
   font-weight: var(--font-bold);
   color: var(--color-black);
   text-decoration: none;
   border-radius: 50%;
   background-color: var(--color-white);
   box-shadow: var(--box-shadow-white);
}